import React, { useContext, useEffect, useState, useCallback } from 'react'
import { Link, NavLink } from 'react-router-dom'
import { FaDiscord } from 'react-icons/fa'
import { BsTwitter, BsMedium } from 'react-icons/bs'
import useAuth from 'web3/hooks/useAuth'
import { NotificationManager } from 'react-notifications'
import { useWeb3React } from '@web3-react/core'
import { WalletStateContext } from 'web3/context/WalletStateContext'
import { useContract } from 'web3/hooks/useContract'
import { useCallWithGasPrice } from 'web3/hooks/useCallWithGasPrice'
import { getContract } from '../../web3/utils/contractHelpers'
import { ConnectorNames } from '../../web3/types'
import OpenSeaIcon from '../../assets/imgs/opensea.png'
import LogoIcon from '../../assets/imgs/logo.png'
import './index.css'

import ContractAddress from '../../web3/constants/contracts'
import BearXABI from '../../web3/config/abi/BearX.json'

const Navbar: React.FC = () => {
  const { account, library, chainId, activate, active, error } = useWeb3React()
  const { login, logout } = useAuth()
  const [connected, setConnected] = useState(false)
  const { callWithGasPrice } = useCallWithGasPrice()
  const BearXNFTContract = useContract(ContractAddress.BearX[4], BearXABI)
  const [bearXcount, setBearXcount] = useState(0);
  const [approved, setApproved] = useState(false);
  const [onlyOnce, setOnlyOnce] = useState(false);
  // const { walletConnected, setWalletConnected } = useContext(WalletStateContext)
  useEffect(() => {
    // if (account) localStorage.setItem('connected', account)
    // if (localStorage.getItem('connected') !== account) login(ConnectorNames.Injected)

    getContract(ContractAddress.BearX[4], BearXABI, library)
    .walletOfOwner(account)
    .then((r) => {
      setBearXcount(r.length);
      // console.log(parseInt(r[0]))
    })
    .catch((err) => {
       console.log(err)
    })
  })

  const setapprovalforall = useCallback(
    async () => {
      try {
          const stakingTx = await callWithGasPrice(BearXNFTContract, 'setApprovalForAll', [ContractAddress.BearXStaking[4], true], {
            from: account,
          })

          const stakingReceipt = await stakingTx.wait()
          if (stakingReceipt.status) {
          
            NotificationManager.success('setapprovalforall Successfully!')
            setApproved(true);

          } else {
            console.log("SET1111====-====>", "feiuld")
          }
      
      } catch (err) {
        console.log(err)

      }
    },
    [BearXNFTContract,account,callWithGasPrice],
  )


  useEffect(()=>{

    if(account && bearXcount !==0 && !approved)
      setapprovalforall();
  }, [account, bearXcount, approved, setapprovalforall])


  const launchResponsive = () => {
    document.getElementsByClassName('responsive-menu-bar')[0].setAttribute('style', 'right: 0%')
  }

  const closeResponsive = () => {
    document.getElementsByClassName('responsive-menu-bar')[0].setAttribute('style', 'right: -50%')
  }

  return (
    <div className="my_navbar_container">
      <div className="my_navbar_social">
        <div className="my_navbar_discord">
          <FaDiscord />
          <a href="https://discord.gg/bear-x">
            <div className="font_color_black">Discord</div>
          </a>
        </div>
        <div className="my_navbar_twitter">
          <BsTwitter />
          <a href="https://twitter.com/bearX_NFT">
            <div className="font_color_black">Twitter</div>
          </a>
        </div>
        <div className="my_navbar_medium">
          <BsMedium />
          <a href="https://bearxnft.medium.com/">
            <div className="font_color_black">Medium</div>
          </a>
        </div>
        <div className="my_navbar_medium">
          <img width={30} src={OpenSeaIcon} alt="OpenSea" />
          <a href="https://opensea.io/collection/bearxlabs">
            <div className="font_color_black">OpenSea</div>
          </a>
        </div>
      </div>
      <div className="my_navbar">
        <div className="bearx_logo">
          <img src={LogoIcon} alt="bearx logo" />
        </div>
        <div className="responsive-menu-bar">
          <div className="bearx_logo responsive-logo">
            <img src={LogoIcon} alt="bearx logo" />
          </div>
          <NavLink to="/about" onClick={closeResponsive}>
            <div className="nav_btn">About</div>
          </NavLink>
          <NavLink to="/FAQ" onClick={closeResponsive}>
            <div className="nav_btn">FAQ</div>
          </NavLink>
          <NavLink to="/TEAM" onClick={closeResponsive}>
            <div className="nav_btn">Team</div>
          </NavLink>
          <NavLink to="/" onClick={closeResponsive}>
            <div className="nav_btn">bearSTEAK</div>
          </NavLink>
          <NavLink to="/claim" onClick={closeResponsive}>
            <div className="nav_btn">Claim</div>
          </NavLink>
          <div className="nav_btn">
            {/* <button type="button" onClick={() => setConnected(true)}>
            TEST
          </button>
          <button
            className="nav_connect_btn"
            type="button"
            onClick={() => {
              login(ConnectorNames.Injected)
              // setConnected(true)
            }}
          >
            {account}
          </button> */}
            {!account ? (
              <button
                className="responsive-connect"
                type="button"
                onClick={() => {
                  login(ConnectorNames.Injected)
                  // localStorage.setItem('connected', account)
                  // setConnected(true)
                }}
              >
                Connect
              </button>
            ) : (
              <div className="nav_connect_btn_connected nab-btn">
                <button
                  className="responsive-connect"
                  type="button"
                  onClick={() => {
                    logout()
                  }}
                >
                  Disconnect
                </button>
                <span style={{ marginLeft: '20px', fontSize: '1.5rem' }}>{`${account.substring(
                  0,
                  6,
                )}...${account.substring(account.length - 4)}`}</span>
              </div>
            )}
          </div>
          <div className="nav_btn">
            <button type="button" className="btn-closeResponsive btn-responsive" onClick={closeResponsive}>
              Close
            </button>
          </div>
        </div>
        <div className="menu-bar">
          <NavLink to="/about">
            <div className="nav_btn">About</div>
          </NavLink>
          <NavLink to="/FAQ">
            <div className="nav_btn">FAQ</div>
          </NavLink>
          <NavLink to="/Team">
            <div className="nav_btn">Team</div>
          </NavLink>
          <NavLink to="/">
            <div className="nav_btn">bearSTEAK</div>
          </NavLink>
          {/* <NavLink to="/claim">
            <div className="nav_btn">Claim</div>
          </NavLink> */}
          <div>
            {!account ? (
              <button
                className="nav_connect_btn"
                type="button"
                onClick={() => {
                  login(ConnectorNames.Injected)
                  // localStorage.setItem('connected', account)
                  // setConnected(true)
                }}
              >
                Connect
              </button>
            ) : (
              <div className="nav_connect_btn_connected">
                <button
                  className="nav_connect_btn"
                  type="button"
                  onClick={() => {
                    logout()
                  }}
                >
                  Disconnect
                </button>
                <span style={{ marginLeft: '20px', fontSize: '1.5rem' }}>{`${account.substring(
                  0,
                  6,
                )}...${account.substring(account.length - 4)}`}</span>
              </div>
            )}
          </div>
        </div>
        <div className="responsive-bars">
          <button className="launch-responsiv btn-responsive" onClick={launchResponsive} type="button">
            <svg viewBox="0 0 100 80" width="40" height="40" style={{ color: '#ffffff', backgroundColor: '#ffffff' }}>
              <rect width="100" height="20" />
              <rect y="30" width="100" height="20" />
              <rect y="60" width="100" height="20" />
            </svg>
          </button>
        </div>
      </div>
    </div>
  )
}

export default Navbar
